<template>
    <main>
        <v-tabs v-model="index1">
            <v-tabs-item></v-tabs-item>
            <v-tabs-item>
                <v-badge value="5"></v-badge>
            </v-tabs-item>
            <v-tabs-item></v-tabs-item>
            <v-tabs-item></v-tabs-item>
        </v-tabs>

        <v-tabs v-model="index2" class="gutter-top">
            <v-tabs-item>
                <v-badge></v-badge>
            </v-tabs-item>
            <v-tabs-item v-for="text in list2" :key="text">{{text}}</v-tabs-item>
        </v-tabs>
    </main>
</template>
<script>
import VTabs from '@/packages/Tabs/Tabs';
import VTabsItem from '@/packages/TabsItem/TabsItem';
import VBadge from '@/packages/Badge/Badge';

export default {
    name: 'TabsDemo',

    data() {
        return {
            index1: 0,
            index2: 1,
            list2: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '6+1']
        };
    },

    methods: {},

    components: {
        VTabs,
        VTabsItem,
        VBadge
    }
};
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
</style>



