<template>
    <a-scroll-view v-model="scrollTop" class="fill">

        <a-cell :has-ripple="true" v-for="n in 15" :key="n+10" @click="scrollTop=9999">
            {{String.fromCharCode(64 + parseInt(n))}}</a-cell>

        <!-- confirm -->
        <a-button  type="success"  @click="isShow1=!isShow1" class="gutter-top trigBtn">
            confirm</a-button>

        <a-popper :is-show.sync="isShow1" target=".trigBtn" class="fill">
            <h4 class="fill-bottom-sm fill-top-sm text-darkest">?</h4>
            <a-button type="dark"  size="sm" @click="showMore" class="gutter-top" ></a-button>
            <a-button type="danger" size="sm"  @click="showMore" class="gutter-top"></a-button>
        </a-popper>

        <!--  -->
        <a-button id="drop-down"  type="info"  @click="isShow2=!isShow2" class="gutter-top">
            <a-icon name="menu" size="16"/>
            </a-button>

        <a-popper :is-show.sync="isShow2" target="#drop-down">
            <a-cell :has-ripple="true" v-for="n in 5" :key="n" @click="isShow2=false">{{String.fromCharCode(64 + parseInt(n))}}</a-cell>
        </a-popper>

        <!-- theme -->
        <a-button id="type-theme"  type="danger" @click="isShow3=true" class="gutter-top">
            type</a-button>

        <a-popper type="danger" :is-show.sync="isShow3" target="#type-theme" class="fill">
            
        </a-popper>

    </a-scroll-view>
</template>
<script>
import AButton from '@/packages/Button/Button';
export default {
    name: 'PopperDemo',

    data() {
        return {
            scrollTop: 0,
            isShow1: false,
            isShow2: false,
            isShow3: false
            
        };
    },

    mounted() {
        this.isShow1 = true;
    },

    methods: {
        showMore(){
            this.scrollTop=9999;
            this.isShow1 = false;
        },
    }
};
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
.popperNode {
    padding: 30px;
    box-shadow: $shadowDown;
}
</style>



