<script>
// vue-lazyload, 
// https://github.com/hilongjw/vue-lazyload
// IntersectionObserver, , caniuse>=5
// https://developer.mozilla.org/zh-CN/docs/Web/API/Intersection_Observer_API
// indexeddbid, .
// , 
import { getWidth, getHeight, getScrollParent, getTime, getIsInView } from '../../utils/dom';
import { getConfig } from '../../packages/config/';
import throttle from 'lodash/throttle';
export default {
    name: 'AtomLazyLoad',

    props: {
        src: {
            type: String,
        },

        placeholderBackgroundColor: {
            type: String,
            default: '#eee',
        },

        placeholder: {
            type: String,
            default() {
                return `data:image/svg+xml;utf8,<svg width="1" height="1" version="1.1" xmlns="http://www.w3.org/2000/svg"><rect width="1" height="1" fill="${this.placeholderBackgroundColor}"/></svg>`;
            },
        },

        throttleTime: {
            type: Number,
            default: 200,
        },

        attempt: {
            type: Number,
            default: 3,
        },

        errorImg: {
            type: String,
            default: '',
        },

        preLoadRate: {
            type: Number,
            default: 1.3,
        },

        events: {
            type: Array,
            default() {
                return ['scroll', 'wheel', 'mousewheel', 'resize', 'animationend', 'transitionend', 'webkitAnimationend', 'webkitTransitionend', 'touchmove'];
            },
        },
    },

    data() {
        return {
            status: 'ready',
            url: this.placeholder,
            costTime: 0,
            attemptCount: 0,
            scrollParentNode: null,
        };
    },

    render(h) {
        return h('img', {
            attrs: {
                src: this.url,
                lazy: this.status,
            },

            class: ['atom-lazyload'],

            // on: {
            //     animationend: this.animationend,
            //     webkitAnimationend: this.animationend
            // }
        });
    },

    created() {
        // 
        // this.url = this.placeholder;
    },

    mounted() {
        // , 
        this.scrollParentNode = getScrollParent(this.$el);
        // 
        this.events.forEach(eventName => {
            this.scrollParentNode.addEventListener(eventName, this.loadImgIfInView);
        });

        this.loadImgIfInView();
        // this.lazyload = () => {
        //     if ('ready' === this.$el.getAttribute('lazy')) {
        //         this.$nextTick(() => {
        //             this.loadImgIfInView();
        //         });
        //     }
        // };
    },

    methods: {
        /**
         * 
         * 
         */
        lazyload() {
            if ('ready' === this.$el.getAttribute('lazy')) {
                this.$nextTick(() => {
                    this.loadImgIfInView();
                });
            }
        },

        /**
         * , 
         * 
         */
        loadImgIfInView() {
            // throttle.cancel?
            throttle(() => {
                if ('loaded' === this.status) {
                    this.events.forEach(eventName => {
                        this.scrollParentNode.removeEventListener(eventName, this.loadImgIfInView);
                    });
                } else if ('ready' === this.status) {
                    if (getIsInView(this.$el, this.preLoadRate)) {
                        this.loadImg();
                    }
                }
            }, 200)();
        },

        loadImg() {
            const isAllowHttps = getConfig('IS_ALLOW_HTTPS');
            const startTime = getTime();
            let img = new Image();
            let src = this.src;
            // https, http
            if(!isAllowHttps) {
                src = src.replace('https', 'http');
            }
            img.src = src;
            // if (img.complete) {
            // this.status = 'loaded';
            // this.url = this.src;
            // this.costTime = getTime() - startTime;
            // this.$emit('loaded', {
            //     width: img.naturalWidth,
            //     height: img.naturalHeight,
            //     url: this.url,
            //     costTime: this.costTime
            // });
            // img = null;
            // return;
            // }

            if (this.attempt > this.attemptCount) {
                this.attemptCount++;
                // placeholder, img.complete,
                // , , 
                this.status = 'loading';
                // 
                img.onload = e => {
                    this.status = 'loaded';
                    this.url = this.src;
                    this.costTime = getTime() - startTime;
                    this.$emit('loaded', {
                        width: img.naturalWidth,
                        height: img.naturalHeight,
                        url: this.url,
                        costTime: this.costTime,
                    });
                    img = null;
                };

                // 
                img.onerror = e => {
                    this.status = 'attempt';
                    this.$emit('attempt', {
                        url: this.url,
                        costTime: this.costTime,
                    });
                    this.loadImg();
                };
            } else {
                if ('' == this.errorImg) {
                    this.url = this.errorImg;
                }
                this.status = 'fail';
                this.$emit('fail', {
                    url: this.url,
                    costTime: this.costTime,
                });
                img = null;
            }

            // lazydisplay:none/blockanimate
            setTimeout(() => {
                this.$el.removeAttribute('lazy');
            }, 1050);
        },
    },

    watch: {
        /**
         * src
         */
        src(src) {
            this.status = 'ready';
            this.loadImgIfInView();
        },
    },

    beforeDestroy() {
        this.events.forEach(eventName => {
            this.scrollParentNode.removeEventListener(eventName, this.loadImgIfInView);
        });
    },
};
</script>


