<template>
    <div @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd" class="atom-carousel">
        <div ref="body" :style="{transform: `translate3d(${translateX}px, 0, 0)`, transitionDuration: `${transitionDuration}ms`}" @transitionEnd="transitionEnd" @webkitTransitionEnd="transitionEnd" class="atom-carousel__body">
            <slot></slot>
        </div>

        <template v-if="hasPageBtn && 1 < pageBtnCount">
            <div v-if="0 == pageTheme" :class="[`atom-carousel__paging-${pageTheme}`]">
                <span v-for="n in pageBtnCount" :key="n" :class="{'paging__button--active': n - 1 === realIndex}" class="paging__button"></span>
            </div>

            <div v-else-if="1 == pageTheme" :class="[`atom-carousel__paging-${pageTheme}`]">
                <span>{{Number(realIndex)+1}}</span>
                <span>/</span>
                <span>{{pageBtnCount}}</span>
            </div> 
        </template>
    </div>
</template>

<script>
import { getWidth, getHeight, getTime } from '../../utils/dom';
import loadImage from '../../utils/loadImage';
import { getConfig } from '../../packages/config/';

/**
 * 1. imgsrc-lazy,
 *    src-lazysrc-lazy,
 *    , , .
 *    lazy-weight, , 
 *    ,
 * 2. tranlateXactiveIndex, activeIndex
 */
export default {
    name: 'AtomCarousel',

    // provide: {
    //     perView: 1
    // },

    props: {
        pageTheme: {
            default: 0,
        },

        value: {
            default: 0,
        },

        isZoom: {
            type: Boolean,
            default: false,
        },

        // 'auto'
        slidesPerView: {
            type: [Number, String],
            default: 1,
        },

        hasPageBtn: {
            type: Boolean,
            default: true,
        },

        isAutoPlay: {
            type: Boolean,
            default: false,
        },

        isLoop: {
            type: Boolean,
            default: false,
        },

        threshold: {
            type: Number,
            default: 15,
        },

        delay: {
            type: [Number, String],
            default: 3000,
        },

        disableOnInteraction: {
            type: Boolean,
            default: false,
        },

        speed: {
            type: Number,
            default: 300,
        },

        loadPrevNext: {
            type: Boolean,
            default: true,
        },

        loadPrevNextAmount: {
            type: Number,
            default: 1,
        },

        width: {
            type: [Number, String],
        },

        height: {
            type: [Number, String],
        },
    },

    data: () => ({
        isDisabled: false,
        realCount: 0,
        count: 0,
        warpWidth: 0,
        isAnimating: false,
        startPointX: 0,
        transitionDuration: 0,
        translateX: 0,
        startTranslateX: 0,
        afterSliderTransitonend: () => {},
        playTimeOutId: null,
        imageStore: [],
        momentum: 0,
        startTime: 0,
        fakeCountOneSide: 0, // fake
        itemInViewCount: 0, // item
        deltaX: 0,
    }),

    mounted() {
        this.updateSize();

        this.realCount = this.$children.length;
        this.itemInViewCount = Math.ceil(this.slidesPerView);
        this.fakeCountOneSide = this.isLoop ? this.itemInViewCount : 0;
        if (this.isLoop) {
            this.$nextTick(() => {
                this.cloneNodeForLoop();
                // cloneNodeForLoop
                this.$nextTick(() => {
                    // slidesPerView > 1
                    // min/maxTranlateX
                    // this.count = this.count + 1 - Math.floor(this.slidesPerView);
                    // activeIndex
                    this.imageToStore();
                });
            });
        } else {
            this.imageToStore();
        }

        this.slideTo(this.value, 0);
        // , , translateEnd
        this.playSlider();

        this.$nextTick(() => {
            this.$emit('init', { pageBtnCount: this.pageBtnCount });
        });

        // // 
        // this.$el.update = ()=>{
        //     this.updateSize();
        // };
    },

    methods: {
        /**
         * 
         */
        updateSize() {
            this.warpWidth = 0 < this.width ? this.width : getWidth(this.$el);
            this.warpHeight = 0 < this.height ? this.height : getHeight(this.$el);
        },

        /**
         * loopdom
         */
        cloneNodeForLoop() {
            this.$nextTick(() => {
                const itemWidth = `${this.warpWidth / this.slidesPerView}px`;
                // itemfake
                const itemCount = this.$children.length;
                const fakeCount = this.fakeCountOneSide;
                for (let i = 0; i < fakeCount; i++) {
                    // 
                    let lastFakeNode = this.$children[i].$el.cloneNode(true);
                    lastFakeNode.style.width = itemWidth;
                    this.$refs.body.appendChild(lastFakeNode);

                    // 
                    let headFakeNode = this.$children[itemCount - 1 - i].$el.cloneNode(true);
                    headFakeNode.style.width = itemWidth;
                    this.$refs.body.insertBefore(headFakeNode, this.$refs.body.firstChild);
                }
            });
        },

        /**
         * lazy-src, img/status
         */
        imageToStore() {
            // lazy-src
            // $children, el. $childrenfake/real
            Array.from(this.$el.querySelectorAll('.atom-carousel-item')).forEach(($item, index) => {
                this.imageStore[index] = [];
                // lazy-src<img/>
                Array.from($item.querySelectorAll('img[lazy-src]')).forEach($imgEl => {
                    // $imgEl.setAttribute('lazy-status', 'ready');
                    this.imageStore[index].push({
                        el: $imgEl,
                        url: $imgEl.attributes['lazy-src'].value,
                        status: 'ready',
                    });
                });
            });
        },

        async loadImageByActiveIndex(activeIndex) {
            await this.$nextTick();
            // 
            const eachImageStore = this.imageStore[activeIndex + this.fakeCountOneSide];
            // active
            if (undefined !== eachImageStore) {
                eachImageStore.forEach(item => {
                    if ('ready' === item.status) {
                        // https
                        const isAllowHttps = getConfig('IS_ALLOW_HTTPS');
                        let src = item.url;
                        // 
                        if (!isAllowHttps) {
                            src = item.url.replace('https', 'http');
                        }
                        loadImage(src, {
                            onInit: () => {
                                item.el.setAttribute('lazy-status', 'loading');
                            },

                            onSuccess: () => {
                                item.el.src = item.url;
                                item.el.setAttribute('lazy-status', 'done');
                                item.el.removeAttribute('lazy-src');
                                item.status = 'done';
                            },
                        });
                    }
                });
            }
        },

        /**
         * (false === isLoop)
         */
        playNoLoopSlider() {
            clearTimeout(this.playTimeOutId);
            this.playTimeOutId = setTimeout(() => {
                const nextIndex = this.activeIndex + 1;
                if (this.pageBtnCount <= nextIndex) {
                    this.slideTo(0);
                } else {
                    this.slideTo(nextIndex);
                }
            }, this.delay);
        },

        /**
         * (true === isLoop)
         */
        playLoopSlider() {
            clearTimeout(this.playTimeOutId);
            this.playTimeOutId = setTimeout(() => {
                if (this.pageBtnCount <= this.activeIndex) {
                    this.slideTo(0, 0, () => {
                        this.slideTo(1);
                    });
                } else {
                    this.slideTo(this.activeIndex + 1);
                }
            }, this.delay);
        },

        /**
         * 
         */
        stopSlider() {
            clearTimeout(this.playTimeOutId);
        },

        /**
         * 
         */
        playSlider() {
            if (this.isAutoPlay && !this.disableOnInteraction) {
                if (this.isLoop) {
                    this.playLoopSlider();
                } else {
                    this.playNoLoopSlider();
                }
            }
        },

        /**
         * bodytranslateX
         */
        getTranslateX() {
            // https://github.com/nolimits4web/Swiper/blob/master/src/utils/utils.js
            // , swiper.jsgetTranslate
            const style = getComputedStyle(this.$refs.body, null);
            const matrix = style.transform.split(',');
            return Math.round(matrix[4]);
        },

        /**
         * 
         */
        touchStart(e) {
            if (this.isDisabled) return;
            e.stopPropagation();
            // e.preventDefault();
            const point = e.touches ? e.touches[0] : e;
            this.startPointX = point.pageX;
            this.startPointY = point.pageY;
            this.startTranslateX = this.translateX;
            this.transitionDuration = 0;
            this.startTime = getTime();
            this.$emit('touchstart');
        },

        /**
         * 
         */
        touchMove(e) {
            if (this.isDisabled) return;
            e.stopPropagation();
            this.stopSlider();
            const point = e.touches ? e.touches[0] : e;
            const deltaX = point.pageX - this.startPointX;
            const deltaY = point.pageY - this.startPointY;
            const absDeltaX = Math.abs(deltaX);
            const absDeltaY = Math.abs(deltaY);
            // X
            if (absDeltaX < absDeltaY + 15) return;

            // , touchend
            this.momentum = 0;

            // 
            const now = getTime();
            if (300 < now - this.startTime) {
                this.startTime = now;
            }

            // , 
            if (this.threshold < absDeltaX) {
                // 
                const translateX = this.startTranslateX + deltaX - Math.sign(deltaX) * this.threshold;
                if (this.minTranslateX >= this.translateX) {
                    this.translateX = this.minTranslateX + (translateX - this.minTranslateX) * 0.2;
                } else if (this.maxTranslateX <= this.translateX) {
                    this.translateX = this.maxTranslateX + (translateX - this.maxTranslateX) * 0.2;
                } else {
                    this.translateX = translateX;
                }
            }

            // , fake/real
            if (this.isLoop) {
                // <=, , index
                // fake
                if (this.minTranslateX >= this.translateX && this.minTranslateX >= this.startTranslateX) {
                    this.slideTo(0, 0);
                } else if (this.maxTranslateX <= this.translateX && this.maxTranslateX <= this.startTranslateX) {
                    this.slideTo(this.realCount - this.fakeCountOneSide, 0);
                }
            }

            // item
            if (this.isAnimating) {
                this.startTranslateX = this.getTranslateX();
                this.translateX = this.startTranslateX;
                this.isAnimating = false;
            }
            // this.startTranslateX = this.getTranslateX();

            this.$emit('touchmove');
        },

        /**
         * 
         */
        touchEnd(e) {
            if (this.isDisabled) return;
            // 
            const point = e.changedTouches ? e.changedTouches[0] : e;
            const deltaX = point.pageX - this.startPointX;
            const absDeltaX = Math.abs(deltaX);
            this.momentum = deltaX / (getTime() - this.startTime);
            this.slideTo(this.activeIndex);
            this.$emit('touchend');
        },
        /**
         * activeIndex
         * @argument {Int} activeIndex
         * @argument {Number} duration 
         * @argument {Function} callback 
         * */
        slideTo(activeIndex, duration = this.speed, callback = () => {}) {
            this.isAnimating = 0 < duration;
            this.transitionDuration = duration;
            let translateX = 0 - (activeIndex + this.fakeCountOneSide) * this.stepWidth;
            // sliderPerView
            if (this.minTranslateX > translateX) {
                translateX = this.minTranslateX;
            } else if (this.maxTranslateX < translateX) {
                translateX = this.maxTranslateX;
            }
            this.translateX = translateX;
            this.startTranslateX = this.translateX;
            this.afterSliderTransitonend = callback;
            if (0 === duration) {
                setTimeout(() => {
                    callback();
                }, 199);
            }
        },

        prev(callback = () => {}) {
            const prevActiveIndex = 0 > this.activeIndex - 1 ? 0 : this.activeIndex - 1;
            this.slideTo(prevActiveIndex, this.speed, callback);
        },

        next(callback = () => {}) {
            const nextActiveIndex = this.lastIndex > this.activeIndex + 1 ? this.activeIndex + 1 : this.lastIndex;
            this.slideTo(nextActiveIndex, this.speed, callback);
        },

        /**
         * , /
         */
        transitionEnd() {
            this.isAnimating = false;
            this.transitionDuration = 0;
            this.afterSliderTransitonend(this.activeIndex);
            // 
            this.playSlider();

            this.$emit('input', this.activeIndex);
        },
    },

    computed: {
        pageBtnCount() {
            if (this.isLoop) {
                return this.realCount;
            } else {
                return Math.ceil((this.realCount * this.stepWidth - this.warpWidth) / this.stepWidth) + 1;
            }
        },

        maxTranslateX() {
            return 0;
        },

        minTranslateX() {
            return (this.isLoop ? 0 : 1) * this.warpWidth - (this.realCount + this.fakeCountOneSide) * this.stepWidth;
        },

        stepWidth() {
            return this.warpWidth / this.slidesPerView;
        },

        lastIndex() {
            return this.realCount - 1;
        },
        /**
         * , 
         */
        activeIndex() {
            let activeIndex;
            // , translateX
            if (0.5 < Math.abs(this.momentum)) {
                if (0 < Math.sign(this.momentum)) {
                    activeIndex = 0 - Math.ceil(this.translateX / this.stepWidth) - this.fakeCountOneSide;
                } else {
                    activeIndex = 0 - Math.floor(this.translateX / this.stepWidth) - this.fakeCountOneSide;
                }
            } else {
                // sliderPerView > 1, 
                // value, round, , ceil
                // , translateXtranslatex, round
                if (this.minTranslateX >= this.translateX) {
                    activeIndex = Math.ceil((0 - this.translateX) / this.stepWidth) - this.fakeCountOneSide;
                } else {
                    activeIndex = Math.round((0 - this.translateX) / this.stepWidth) - this.fakeCountOneSide;
                }
            }

            if (this.realCount + this.fakeCountOneSide <= activeIndex) {
                activeIndex = this.realCount + this.fakeCountOneSide - 1;
            } else if (0 - this.fakeCountOneSide > activeIndex) {
                activeIndex = 0 - this.fakeCountOneSide;
            }
            return activeIndex;
        },

        realIndex() {
            let realIndex = this.activeIndex;
            if (this.count <= this.activeIndex) {
                realIndex = 0;
            } else if (-1 >= this.activeIndex) {
                realIndex = this.count + this.activeIndex;
            }
            return realIndex;
        },
    },

    watch: {
        value(value) {
            this.slideTo(value);
        },

        activeIndex(activeIndex) {
            this.$emit('update:realIndex', this.realIndex);
            this.$emit('change', {
                realIndex: this.realIndex,
                activeIndex: this.activeIndex,
            });
        },
        /**
         * lazyload
         * watchrealIndex(this.$emit('input')), active, 
         */
        async realIndex(realIndex) {
            // this.imageStore
            await this.$nextTick();
            // 
            // this.itemInViewCount, isLoop = false, fakeCountOneSide = 0 ,  
            for (let i = 0; i < this.itemInViewCount; i++) {
                let activeIndex = realIndex + i;
                this.loadImageByActiveIndex(activeIndex);
            }

            // next/prev
            if (this.loadPrevNext && 0 < this.loadPrevNextAmount) {
                for (let i = 1; i <= this.loadPrevNextAmount; i++) {
                    this.loadImageByActiveIndex(realIndex - i);
                    this.loadImageByActiveIndex(realIndex + i);
                }
            }

            // fake
            if (this.isLoop) {
                if (0 === realIndex) {
                    for (let i = 0; i < this.fakeCountOneSide; i++) {
                        this.loadImageByActiveIndex(this.realCount + i);
                    }
                } else if (this.lastIndex === realIndex) {
                    for (let i = 1; i <= this.fakeCountOneSide; i++) {
                        this.loadImageByActiveIndex(0 - i);
                    }
                }
            }
        },
    },
};
</script>


