/**
 * 
 * @param {Object} 
 */
const getVLength = (v) => {
    return Math.sqrt(v.x * v.x + v.y * v.y);
}

/**
 * 
 * @param {Object} v1
 * @param {Object} v2
 */
const getDotProduct = (v1, v2) => {
    return v1.x * v2.x + v1.y * v2.y;
}

/**
 * ()
 * @param {Object} v1
 * @param {Object} v2
 */
const getRadian = (v1, v2) => {
    var mr = getVLength(v1) * getVLength(v2);
    if (mr === 0) return 0;
    var r = getDotProduct(v1, v2) / mr;
    if (r > 1) r = 1;
    return Math.acos(r);
}

/**
 * 
 * /
 */
const getCross = (v1, v2) => {
    return v1.x * v2.y - v2.x * v1.y;
}

/**
 * ()
 * @param {Object} v1
 * @param {Object} v2
 */
const getAngle = (v1, v2) => {
    var angle = getRadian(v1, v2);
    if (getCross(v1, v2) > 0) {
        angle *= -1;
    }
    return angle * 180 / Math.PI;
}

/**
 * 
 * @param {Array}  
 */
const getCenter = (points) => {
    const pointLength = points.length;
    if (1 < pointLength) {
        let x = 0;
        let y = 0;
        let i = 0;
        while (i < pointLength) {
            x += points[i].clientX;
            y += points[i].clientY;
            i++;
        }

        return {
            x: Math.round(x / pointLength),
            y: Math.round(y / pointLength)
        };
    }
};

/**
 * 
 * @param {Number} X 
 * @param {Number} Y 
 */
const getDirection = (offsetX, offsetY) => {
    if (offsetX === offsetY) {
        return 'none';
    } else if (Math.abs(offsetX) > Math.abs(offsetY)) {
        return 0 < offsetX ? 'right' : 'left';
    } else {
        return 0 < offsetY ? 'down' : 'up';
    }
};

export {
    getDirection,
    getVLength,
    getDotProduct,
    getRadian,
    getCross,
    getAngle,
    getCenter
};


