<template>
    <main>
        <v-cell>
            <v-switch v-model="isSelect1"></v-switch>
        </v-cell>
        <v-cell>
            <v-switch v-model="isSelect2"></v-switch>
        </v-cell>
        <v-cell>
            <v-switch v-model="isSelect3" :disabled="true"></v-switch>
        </v-cell>
        <v-cell>
            <v-switch v-model="isSelect4" :disabled="true"></v-switch>
        </v-cell>
    </main>
</template>
<script>
import VSwitch from '@/packages/Switch/Switch';
import VCell from '@/packages/Cell/Cell';
export default {
    name: 'SwitchDemo',

    data() {
        return {
            isSelect1: true,
            isSelect2: false,
            isSelect3: true,
            isSelect4: false,
        };
    },

    components: {
        VCell, VSwitch
    }
}
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
</style>



