<template>
    <a-scroll-view class="demo-page">
        <a-cell @click="$toast('2')">, 2</a-cell>
        <a-cell @click="$toast('2', {type:'success'})"><a class="text-success"></a>, 2</a-cell>
        <a-cell @click="$toast('2', {type:'warning'})"><a class="text-warning"></a>, 2</a-cell>
        <a-cell @click="$toast('2', {type:'danger'})"><a class="text-danger"></a>, 2</a-cell>
        <a-cell @click="$toast('2', {type:'loading'})">, 2</a-cell>
        <section class="fill">
            <a-button @click="test"></a-button>
        </section>
    </a-scroll-view>
</template>
<script>
export default {
    name: 'GlboalToastDemo',

    data() {
        return {
            activeIndex: 0,
        };
    },

    methods: {
        test() {
            this.$toast('test', { time: -1 });
            setTimeout(() => {
                this.$toast.close();
            }, 1000);
        },
    },
};
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
.demo-page {
    height: calc(100% - 55px);
}
</style>



