<template>
    <main class="fill">
        <a-button :is-ghost="true" :is-block="true" type="primary" @click="confirm" class="gutter-top">
            Confrim
        </a-button>
    </main>
</template>
<script>
export default {
    name: 'ConfrimDemo',

    methods: {
        confirm(){
            this.$confirm('atom?', {
                onOk(){
                    this.$alert('ok');
                },

                onCancel(){
                    this.$alert('cancel');
                }
            })
        }
    }
};
</script>
<style scoped lang="scss">
</style>



