<template>
    <main class="demo-page">
        <a-group>
            <template slot="header">
                
                <span class="text-danger">arrow</span>, 
            </template>

            <a-cell arrow-direction="up">arrow-direction=up</a-cell>
            <a-cell arrow-direction="right">arrow-direction=right</a-cell>
            <a-cell arrow-direction="down">arrow-direction=down</a-cell>
            <a-cell arrow-direction="left">arrow-direction=left</a-cell>
        </a-group>

        <a-group>
            <template slot="header">
                <span class="text-danger">extra</span>/
            </template>
            <a-cell>
                <template slot="append"></template>
            </a-cell>
            <a-cell>
                <template slot="prepend"></template>
            </a-cell>
        </a-group>
        <a-group>
            <template slot="header">
                <span class="text-danger">switch/radio/checkbox</span>
            </template>

            <a-cell>
                <a-switch></a-switch>
            </a-cell>
            <a-cell>
                <a-checkbox></a-checkbox>
            </a-cell>

            <a-cell>
                <a-radio v-model="radioValue" value="1"></a-radio>
            </a-cell>

            <a-cell>
                <a-radio v-model="radioValue" value="2"></a-radio>
            </a-cell>

            <a-cell>
                <a-input v-model="text" placeholder=""/>
            </a-cell>
        </a-group>
    </main>
</template>
<script>
export default {
    name: 'CellDemo',

    data() {
        return {
            text: '',
            radioValue: 1
        };
    }
};
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
.demo-page{height: calc(100% - 55px);background: $lightest;}
</style>



