let instances = {};
/**
 * vue
 * @param {Object} Vue
 * @param {Object}  
 */
export default function(Vue, component, {
    props = {},
    on = {},
    isSingle = true
} = {}) {
    const id = `_app-${component.name}`;
    if (!isSingle || undefined === instances[id]) {
        // 
        const node = document.createElement('div');
        node.id = id;
        // 
        document.body.appendChild(node);
        // 
        const VueSubClass = Vue.extend(component);
        // 
        let vm = new VueSubClass();
        // 
        for (let key in props) vm[key] = props[key];
        // 
        for (let eventName in on){
            vm.$off(eventName)
            vm.$on(eventName, on[eventName]);
        } 
        // 
        vm.$mount(`#${node.id}`);
        instances[id] = vm;
        return vm;
    } else {
        let vm = instances[id];
        for (let key in props) vm[key] = props[key];
        for (let eventName in on){
            vm.$off(eventName)
            vm.$on(eventName, on[eventName]);
        } 
        return vm;
    }
};


