<template>
    <transition name="fadeUp">
        <span v-show="isShow" :style="{padding: `${padding}px`}" :class="[type && 'atom-popper--'+type]" class="atom-popper">
            <slot>{{content}}</slot>
        </span>
    </transition>
</template>

<script>
import Popper from 'popper.js';
export default {
    name: 'AtomPopper',

    props: {
        type:{
            type: String
        },

        // 
        options: {
            type: Object,
            default: () => {}
        },

        isShow: {
            type: Boolean,
            default: false
        },

        placement: {
            type: String,
            default: 'auto'
        },

        isCloseAfterClick: {
            type: Boolean,
            default: true
        },

        content: {
            type: String,
            default: ''
        },

        // 
        padding: {
            type: Number
        },

        // 
        target: {
            type: String
        }
    },

    render(h) {
        return h(
            'transition',
            {
                props: { name: 'fadeUp' }
            },
            [
                h(
                    'span',
                    {
                        class: ['atom-popper'],

                        style: {
                            display: this.isShow ? undefined : 'none',
                            padding: `${this.padding}px`
                        }
                    },

                    this.content || this.$slots.default
                )
            ]
        );
    },

    data() {
        return {
            popper: undefined,
            referenceNode: undefined,
            popperNode: undefined,
            arrowNode: undefined,
            isTrigerByCommond: false // $toolTip
        };
    },

    mounted() {
        // popper, close
        if (this.isCloseAfterClick) {
            // touchstart,?
            document.addEventListener('touchstart', this.close);
            document.addEventListener('click', this.close);
        }
    },

    methods: {
        isTargetInContainer(e) {
            //  && popperbody, reference/popper
            return (
                !this.referenceNode.contains(e.target) &&
                !this.popperNode.contains(e.target)
            );
        },

        close(e) {
            e.stopPropagation();
            if (this.isShow) {
                // popper
                if (this.isTargetInContainer(e)) {
                    // $toolTip, vueprop, 
                    if (this.isTrigerByCommond) {
                        this.isShow = false;
                    }
                    this.$emit('update:isShow', false);
                }
            }
        },

        /**
         * dom
         */
        _insertArrow() {
            if (undefined !== this.arrowNode) return;
            this.arrowNode = document.createElement('div');
            this.arrowNode.setAttribute('x-arrow', '');
            this.arrowNode.className = 'popper__arrow';
            this.popperNode.appendChild(this.arrowNode);
        },

        /**
         * popper
         */
        createPopper() {
            // dom
            this.referenceNode =
                this.referenceNode || document.querySelector(this.target);
            this.popperNode = this.$el;
            // 
            this._insertArrow();
            this.popper = new Popper(this.referenceNode, this.popperNode, {
                placement: this.placement,
                removeOnDestroy: true,
                modifiers: {
                    // tanslate, 
                    computeStyle: { gpuAcceleration: false }
                },
                onUpdate: (data, options) => {
                    this.$emit('update', { data, options });
                }
            });
        }
    },

    watch: {
        async isShow(isShow) {
            if (!isShow) return;
            await this.$nextTick();
            if (undefined !== this.referenceNode) {
                this.popper.scheduleUpdate();
            } else {
                this.createPopper();
            }
        }
    },

    breforeDestory() {
        document.removeEventListener('click', this.close);
        document.removeEventListener('touchstart', this.close);
        this.popper.destroy();
        this.popper = null;
    }
};
</script>


