<template>
    <div 
        :error="hasError" 
        :loading="isShowLoading" 
        class="atom-input"
        :empty="isEmpty"
        >

        <!--  -->
        <slot name="prepend"></slot>

        <!--  -->
        <slot></slot>

        <!-- input -->
        <input 
            ref="input" 
            v-bind="$attrs" 
            :aria-disabled="$attrs.disabled"
            :aria-placeholder="$attrs.placeholder" 
            v-model="text"
            @focus="focus" 
            @blur="blur" 
            @keyup="keyup" 
            @keydown="keydown"
            @change="change"
            class="atom-input__input">
        
        <!--  -->

        <!--  -->
        <transition name="fadeLeft">
            <a-icon 
                v-if="clearable" 
                name="close" 
                size="14" 
                v-show="isShowClearBtn"
                @click="clear" 
                class="atom-input__btn-empty"/>
        </transition>

        <template v-if="hasFeedback">
            <!--  -->
            <i class="atom-input__loading"/>

            <!--  -->
            <span class="atom-input__error">
                <a-icon name="warning" size="14"/>
                <span  class="error-message">
                    {{errorMessage}}
                    <i class="triangle triangle-danger"></i>
                </span>
            </span>
        </template>


        <!--  -->
        <slot name="append"></slot>
    </div>
</template>
<script>
// 1. input, keyupinput
// 2. keydowne.target.value,
// 3. keyupvalue,
// 4. setTimeout
import AIcon from '../../packages/Icon';
export default {
    name: 'AtomInput',

    components: { AIcon },

    props: {
        // focus
        isSelectAll: {
            type: Boolean,
            default: false,
        },
        clearable: {
            type: Boolean,
            default: true,
        },

        value: {
            required: true,
        },

        // bankCode | letter | phone | number
        type: {
            type: String,
        },

        // , 
        hasFeedback: {
            type: Boolean,
            default: true,
        },

        // 
        rules: {
            type: Array,
            default: () => [],
        },

        // 
        filter: {
            type: RegExp,
        },
    },

    computed: {
        isEmpty() {
            return undefined !== this.text && 0 === this.text.length;
        },
    },

    data() {
        return {
            text: '',
            isShowClearBtn: false,
            hasError: false,
            errorMessage: '',
            rulesGroupByEvent: {},
            isShowLoading: false,
        };
    },

    methods: {
        /**
         * 
         * @argument {String} 
         * @returns {String} 
         */
        filterInput(string) {
            return undefined !== this.filter ? string.replace(this.filter, '') : string;
        },

        /**
         * 
         */
        _validate(rule) {
            return new Promise((resolve, reject) => {
                // 
                if (rule.required) {
                    // 
                    if ('' !== this.text) {
                        resolve(true);
                    } else {
                        reject(rule.message);
                    }
                } else if (undefined !== rule.test) {
                    // 
                    if (rule.test.test(this.text)) {
                        resolve(true);
                    } else {
                        reject(rule.message);
                    }
                } else if (undefined !== rule.minLength) {
                    if (rule.minLength <= this.text.length) {
                        resolve(true);
                    } else {
                        reject(rule.message);
                    }
                } else if (undefined !== rule.maxLength) {
                    // maxlength
                    if (rule.maxLength >= this.text.length) {
                        resolve(true);
                    } else {
                        reject(rule.message);
                    }
                } else if (undefined !== rule.validator) {
                    // []
                    if (rule.validator()) {
                        resolve(true);
                    } else {
                        reject(rule.message);
                    }
                } else if (undefined !== rule.asyncValidator) {
                    // []
                    this.isShowLoading = true;
                    rule.asyncValidator(({ isPass, message }) => {
                        this.isShowLoading = false;
                        if (isPass) {
                            resolve(true);
                        } else {
                            reject(message);
                        }
                    });
                }
            });
        },

        /**
         * 
         */
        async validateInEvent(eventName) {
            let rules = this.rulesGroupByEvent[eventName];
            // rule, 
            if (undefined === rules) return;

            let isAllPass = true;
            // , 
            // , , await
            for (let rule of rules) {
                try {
                    await this._validate(rule);
                } catch (error) {
                    isAllPass = false;
                    this.showErrorDialog(error);
                    break;
                }
            }
            // , 
            if (isAllPass) {
                this.hideErrorDialog();
            }
        },

        /**
         * 
         */
        validate(rules) {
            let isAllPass = true;
            return new Promise(async (resolve, reject) => {
                // rules, 
                for (let rule of (rules || this.rules)) {
                    try {
                        await this._validate(rule);
                    } catch (error) {
                        isAllPass = false;
                        this.showErrorDialog(error);
                        reject(error);
                        break;
                    }
                }

                // 
                if (isAllPass) {
                    this.hideErrorDialog();
                    resolve(true);
                }
            });
        },

        /**
         * 
         */
        clearValidate() {
            this.isShowLoading = false;
            this.hideErrorDialog();
            this.$emit('clear-validate');
        },

        /**
         * 
         * @argument {String} 
         */
        showErrorDialog(message) {
            this.hasError = true;
            this.errorMessage = message;
            this.$emit('error', message);
        },

        /**
         * 
         */
        hideErrorDialog() {
            this.hasError = false;
            this.errorMessage = '';
            this.$emit('success');
        },

        focus(e) {
            this.isFocused = true;
            // 
            if (this.isSelectAll) {
                e.target.select();
            }
            if ('' !== this.text) {
                this.isShowClearBtn = true;
            }
            this.$emit('focus', e);
        },

        blur(e) {
            this.validateInEvent('blur');
            this.isShowClearBtn = false;
            this.$emit('blur', e);
        },

        change(e){
            this.$emit('change', e);
        },

        keyup(e) {
            // 
            let value = this.filterInput(this.text);

            if ('bankCode' == this.type) {
                value = value.replace(/\D/g, '').replace(/(....)(?=.)/g, '$1 ');
            } else if ('letter' == this.type) {
                value = value.replace(/\d/g, '');
            } else if ('phone' == this.type) {
                value = value.replace(/\D/g, '').substring(0, 11);
                const valueLen = value.length;
                if (valueLen > 3 && valueLen < 8) {
                    value = `${value.substr(0, 3)} ${value.substr(3)}`;
                } else if (valueLen >= 8) {
                    value = `${value.substr(0, 3)} ${value.substr(3, 4)} ${value.substr(7)}`;
                }
            } else if ('number' == this.type) {
                value = value.replace(/\D/g, '');
            }
            this.text = value;
            this.$emit('keyup', e);
            this.$emit('input', value);
            // , 
            this.$forceUpdate();
            this.validateInEvent('keyup');
        },

        /**
         * 
         */
        keydown(e) {
            this.$emit('keydown');
        },

        /**
         * input
         */
        clear() {
            this.$emit('input', '');
            this.$emit('clear');
            this.$refs.input.focus();
        },
    },

    watch: {
        value(value) {
            // 
            this.text = value;
            if ('' == value) {
                this.isShowClearBtn = false;
            } else {
                this.isShowClearBtn = true;
            }
        },
    },

    created() {
        // 
        this.text = this.value;
    },

    mounted() {
        this.rules.forEach(rule => {
            // blur
            const eventName = rule.trigger || 'blur';
            if (undefined === this.rulesGroupByEvent[eventName]) {
                this.rulesGroupByEvent[eventName] = [];
            }
            this.rulesGroupByEvent[eventName].push(rule);
        });
    },
};
</script>


