<script>
export default {
    name: 'AtomLoadingRipple',
    
    // class
    // functional: true,
    
    render(h){
        return h('i', {
            class: ['atom-loading-ripple', `atom-loading-ripple--${this.type}`]
        })
    },

    props: {
        type: {
            type: String,
            default: 'primary'
        }
    }
};
</script>


