<template>
    <a-scroll-view>
        <a-cell>
            <a-switch v-model="isAccordion">accordion</a-switch>
        </a-cell>
        
        <a-group title="">
            <a-collapse :is-accordion="isAccordion">
                <a-collapse-item v-for="n in 3" :key="n" :title="`${n}`">
                    {{n}}
                </a-collapse-item>
            </a-collapse>
        </a-group>

        <a-group title="">
            <a-collapse :is-accordion="isAccordion">
                <a-collapse-item :is-open="true">
                    <template slot="header">
                        props: is-accordion
                    </template>
                    is-accordion="ture", 
                </a-collapse-item>
                <a-collapse-item title="props: title" :is-open="false">
                    collapse-itemtitle/is-open
                </a-collapse-item>
                <a-collapse-item title="slot: header" :is-open="false">
                    , collapse-itemname="header"slot
                </a-collapse-item>
            </a-collapse>
        </a-group>


    </a-scroll-view>
</template>
<script>
export default {
    name: 'CollapseDemo',

    data() {
        return {
            isAccordion: true
        };
    },
};
</script>
<style scope lang="scss">
@import '../scss/variables.scss';

</style>


