<template>
    <v-group class="fill">
        <v-steps v-model="activeIndex">
            <v-steps-item>iconlabel</v-steps-item>
            <v-steps-item></v-steps-item>
            <v-steps-item> (autofocus  Safari )input </v-steps-item>
        </v-steps>
        <v-button  @click="prev"></v-button>
        <v-button class="gutter-top" @click="next"></v-button>
    </v-group>
</template>
<script>
import VGroup from '@/packages/Group/Group';
import VButton from '@/packages/Button/Button';
import VSteps from '@/packages/Steps/Steps';
import VStepsItem from '@/packages/StepsItem/StepsItem';

export default {
    name: 'StepsDemo',

    data() {
        return {
            activeIndex: 0
        };
    },

    methods: {
        prev() {
            this.activeIndex = Math.max(0, this.activeIndex - 1);
        },

        next() {
            this.activeIndex = Math.min(2, this.activeIndex + 1);
        }
    },

    components: {
        VStepsItem,
        VSteps,
        VButton,
        VGroup
    }
};
</script>
<style scope lang="scss">
@import '../scss/variables.scss';
</style>



