<template>
    <main class="fill">
        
        <a-text-field 
            v-model="text1"
            :rules="rules"
            ref="inputSchool" 
            label="">
            <template slot="label">
                <a-icon name="star" size="16" />
                
            </template>
            
            <a-icon slot="append" @click="$alert('append')" name="menu" size="16"/>
        </a-text-field>

        <a-text-field label=""  maxlength="19" v-model="text2" type="bankCode" />

        <a-text-field label=" " :is-select-all="true" maxlength="13" v-model="text3" type="phone"/>

        <a-text-field label=""  maxlength="13" v-model="text4" type="number"/>

        <a-text-field label="" maxlength="13" v-model="text5" type="letter" />

        <a-text-field label="(x/y/z)" :filter="/[^x-z]/g" v-model="text6"/>

        <a-button class="gutter-top" @click="validate"></a-button>

        <a-button type="dark" class=" gutter-top gutter-bottom" @click="clearValidate"></a-button>
    </main>
</template>
<script>
export default {
    name: 'TextFieldDemo',

    data() {
        const RULE_1 = { required: true, message: '!' };
        const RULE_2 = { trigger: 'keyup', minLength: 2, message: '2!' };
        const RULE_3 = { trigger: 'keyup', maxLength: 3, message: '3!' };
        const RULE_4 = { trigger: 'keyup', message: '100', validator: this.isSame };
        const RULE_5 = { test: /a/, message: 'a!' };
        const RULE_6 = { test: /b/, message: 'b!' };
        const RULE_7 = { asyncValidator: this.asyncValidator };

        return {
            text1: '',
            text2: '0000 0000 0000 0000',
            text3: '133123456789',
            text4: '01234567',
            text5: 'abc',
            text6: 'xyz',
            isShowWarning: true,
            rules: [RULE_1, RULE_2, RULE_3, RULE_4, RULE_5, RULE_6, RULE_7],
        };
    },

    methods: {
        validate() {
            // this.$loading();
            this.$refs.inputSchool
                .validate()
                .then(() => {
                    this.$toast(`!`, { type: 'success' });
                    // this.$loading.close();
                })
                .catch(error => {
                    // this.clearValidate();
                    this.$toast(`: ${error}`, { type: 'error', delay: 1000 });
                });
        },

        clearValidate() {
            this.$refs.inputSchool.clearValidate();
        },

        isSame() {
            return 100 != this.text1;
        },

        /**
         * 
         */
        asyncValidator(callback) {
            setTimeout(() => {
                callback({ isPass: 'nba' != this.text1, message: 'nba!' });
            }, 1000);
        },
    },
};
</script>
<style lang="scss" scoped>
@import '../scss/variables.scss';
</style>



