import AToast from './Toast';
// 
import createApp from '@/utils/createApp';
import Vue from 'vue';

let vm;
/**
 * Alert
 * @param {Object}}  
 * @param {Object}  
 */
const Toast = (content, {
    type = '',
    position = 'center',
    time = 2000,
    closeable = true
} = {}) => {
    vm = createApp(Vue, AToast);
    vm.type = type;
    vm.position = position;
    vm.closeable = closeable;
    vm.content = content;
    vm.time = time;
    // 2isShow, watch
    vm.$nextTick(() => {
        vm.isShow = true;
    });
    // 
    vm.$on('update:isShow', isShow => {
        vm.isShow = isShow;
    });

    // 
    vm.close = () => {
        vm.isShow = false;
    };
    return vm;
};

Toast.close = () => {
    Vue.nextTick(() => {
        vm.isShow = false;
        // vm.$destroy();
    });
};

// : this.$toast | this.$toast.close()
Vue.prototype.$toast = Toast;

Vue.prototype.$loading = (text)=>{
    Toast(text, {time:0, type:'loading'});
};

Vue.prototype.$loading.close = Toast.close;
export default Toast;


