<template>
    <main 
        :style="{pointerEvents: pointerEvents}"
        class="atom-toast">
        <!-- toast -->
        <transition name="zoom">
            <span v-show="isShow" class="atom-toast__box">
                <a-loading 
                    v-if="'loading' === type" 
                    type="lightest" 
                    name="android"
                    class="gutter-auto d-block"
                    :class="{'gutter-bottom-sm': '' !== content && undefined !== content}"
                    />
                <a-icon 
                    v-else-if="iconName" 
                    :name="iconName" 
                    class="gutter-auto d-block"/>
                {{content}}
            </span>
        </transition>
    </main>
</template>

<script>
import AIcon from '../../packages/Icon';
import ALoading from '../../packages/Loading/Loading';
export default {
    name: 'AtomToast',

    components: {
        ALoading,
        AIcon,
    },

    props: {
        // 
        isShow: {
            type: Boolean,
            default: false,
        },

        // , vnode
        content: {
            type: String,
        },

        // , 0
        time: {
            type: Number,
            default: 2000,
        },

        // , ///loading
        type: {
            type: String,
        },

        // 
        // closeable: {
        //     type: Boolean,
        //     default: true,
        // },
    },

    computed: {
        pointerEvents() {
            return this.isShow ? 'auto' : 'none';
        },

        iconName() {
            let iconMap = {
                success: 'tick',
                warning: 'warning',
                danger: 'close',
                error: 'close',
            };
            return iconMap[this.type];
        },
    },

    data() {
        return {
            isShowMask: false,
            isShowBox: false,
            timeoutId: null,
        };
    },

    methods: {
        close() {
            // clearTimeout(this.timeoutId);
            // this.$emit('update:isShow', false);
        },
    },

    watch: {
        // false,
        // $toasttrue,
        // watch
        isShow(isShow) {
            document.documentElement.classList.toggle('ovh', isShow);
            document.body.classList.toggle('ovh', isShow);
            if (isShow) {
                // 
                if (0 < this.time) {
                    this.timeoutId = setTimeout(() => {
                        // this.$emit('timeout', this.timeoutId);
                        this.$emit('update:isShow', false);
                    }, this.time);
                }
            }
        },
    },
};
</script>


