/**
 * , 
 * @param {Event} event 
 */
export default function(event) {
    event.stopPropagation();
    const $el = event.currentTarget;
    // 
    if('true' === $el.dataset.rippleIsDisabled) {
        return;
    }
    $el.dataset.rippleCancel = 'true';
}


