<template>
    <main>
        <a-cell>
            <template slot="title"></template>
            <a-textarea 
                :maxlength="10"
                ref="test" 
                :autofocus="true" 
                @focus="focus" 
                @blur="blur" 
                @change-length="changeLength"
                @change-left-length="changeLeftLength"
                v-model="content1">
                <span slot="append" class="fill-right-sm">
                    {{leftLength1}} left
                </span>
            </a-textarea>
        </a-cell>

        <a-cell>
            <template slot="title"></template>
            <a-textarea :filter-exp="exp" v-model="content2"/>
        </a-cell>

    </main>
</template>
<script>
export default {
    name: 'TextareaDemo',

    data() {
        return {
            exp: new RegExp('[a-zA-Z]', 'g'),
            length1: 0,
            leftLength1: 0,
            content1: 'vue atom',
            content2: '',
        };
    },

    methods: {
        changeLeftLength(leftLength) {
            this.leftLength1 = leftLength;
        },

        changeLength(length) {
            this.length1 = length;
        },

        focus(e) {
            console.dir(e);
        },

        blur(e) {
            console.dir(e);
        },
    },
};
</script>
<style scoped lang="scss">
</style>



