<template>
    <main >
        <a-cell>
            <template slot="prepend">  *</template>
            <a-input 
                class="padding-left" 
                ref="inputSchool" 
                required 
                :rules="rules" 
                placeholder="" 
                v-model="text1">
                <a-icon  slot="append" @click="$alert('append')" name="menu" size="16" />
            </a-input>
        </a-cell>

        <a-cell >
            <template slot="prepend"> *</template>
            <a-input 
                class="padding-left" 
                ref="inputName" 
                required 
                :rules="rules2" 
                placeholder="" 
                v-model="text11">
                <a-icon  slot="append" @click="$alert('append')" name="menu" size="16" />
            </a-input>
        </a-cell>

        <a-cell>
            <template slot="prepend"></template>
            <a-input class="padding-left" maxlength="19" v-model="text2" type="bankCode" />
        </a-cell>

        <a-cell>
            <template slot="prepend"> </template>
            <a-input class="padding-left" :is-select-all="true" maxlength="13" v-model="text3" type="phone" />
        </a-cell>

        <a-cell>
            <template slot="prepend">  </template>
            <a-input class="padding-left" maxlength="13" v-model="text4" type="number" />
        </a-cell>
        
        <a-cell>
            <template slot="prepend"></template>
            <a-input class="padding-left" maxlength="13" v-model="text5" type="letter" />
        </a-cell>
        
        <a-cell>
            <template slot="prepend">(x/y/z)</template>
            <a-input class="padding-left" :filter="/[^x-z]/g" v-model="text6"/>
        </a-cell>

        <section class="fill">
            <a-button class="gutter-top" @click="validateBoth"></a-button>
            
            <a-button type="dark" class="gutter-top gutter-bottom" @click="clearValidate"></a-button>
        </section>
    </main>
</template>
<script>
export default {
    name: 'InputDemo',

    data() {
        const RULE_1 = { required: true, message: '!' };
        const RULE_2 = { trigger: 'keyup', minLength: 2, message: '2!' };
        const RULE_3 = { trigger: 'keyup', maxLength: 3, message: '3!' };
        const RULE_4 = { trigger: 'keyup', message: '100', validator: this.isSame };
        const RULE_5 = { test: /a/, message: 'a!' };
        const RULE_6 = { test: /b/, message: 'b!' };
        const RULE_7 = { asyncValidator: this.asyncValidator };

        return {
            text1: '',
            text11: '',
            text2: '0000 0000 0000 0000',
            text3: '133123456789',
            text4: '01234567',
            text5: 'abc',
            text6: 'abc',
            isShowWarning: true,
            rules: [RULE_1, RULE_2, RULE_3, RULE_4, RULE_5, RULE_6, RULE_7],
            rules2: [{ required: true, message: '!' }],
        };
    },

    methods: {
        /**
         * 
         */
        async validateBoth() {
            const _$refs = [this.$refs.inputSchool, this.$refs.inputName];
            for ([key, $input] of _$refs.entries()) {
                try {
                    await $input.validate();
                    this.$toast(`!`, { type: 'success' });
                } catch (error) {
                    this.$toast(`: ${error}`, { type: 'error' });
                    break;
                }
            }
        },

        clearValidate() {
            this.$refs.inputSchool.clearValidate();
            this.$refs.inputName.clearValidate();
        },

        isSame() {
            return 100 != this.text1;
        },

        /**
         * 
         */
        asyncValidator(callback) {
            setTimeout(() => {
                callback({ isPass: 'nba' != this.text1, message: 'nba!' });
            }, 1000);
        },
    },
};
</script>
<style lang="scss" scoped>
@import '../scss/variables.scss';
</style>



