<template>
    <main class="demo-page fill">
        <p class="text-center">: {{activeValue}}</p>
        <a-text-field readonly label="" v-model="activeLabel.year" @focus="showPicker('year')"></a-text-field>
        <a-text-field readonly label="" v-model="activeLabel.month" @focus="showPicker('month')"></a-text-field>

        <a-button type="primary" outline  @click="showPickerAll" class="gutter-top"></a-button>
    </main>
</template>
<script>
import dayjs from 'dayjs';
export default {
    name: 'ApiPickerDemo',

    data() {
        return {
            isShow: false,
            year: [{ label: '2019', value: 2019 }, { label: '2018', value: 2018 }, { label: '2017', value: 2017 }],
            month: [{ label: '1', value: 1 }, { label: '2', value: 2 }, { label: '3', value: 3 }],
            activeLabel: { year: '2018', month: '2' },
            activeValue: { year: 2018, month: 2 },
        };
    },

    methods: {
        showPicker(key) {
            this.$picker([this[key]], {
                value: [this.activeValue[key]], // 
                onOk: acitves => {
                    let { value, label } = acitves[0];
                    this.activeValue[key] = value;
                    this.activeLabel[key] = label;
                },
            });
        },

        showPickerAll() {
            this.$picker([this.year, this.month], {
                value: [this.activeValue.year, this.activeValue.month], // 
                onOk: value => {
                    this.$alert(value);
                },
            });
        },
    },
};
</script>
<style scoped lang="scss">
@import '../scss/variables.scss';
.demo-page {
    height: calc(100% - 55px);
}
</style>



