import ALoading from './Loading';
export default ALoading;

import AToast from '@/packages/Toast/Toast.vue';
// 
import createApp from '@/utils/createApp';
import Vue from 'vue';
let vm;
/**
 * Alert
 * @param {Object}}  
 * @param {Object}  
 */
export const Loading = (content, {
    type = 'loading',
    position = 'center',
    delay = 0,
    closeable = true
} = {}) => {
    vm = createApp(Vue, AToast);
    vm.type = type;
    vm.position = position;
    vm.closeable = closeable;
    vm.content = content;
    vm.delay = delay;
    // 2isShow, watch
    vm.$nextTick(() => {
        vm.isShow = true;
    });
    // 
    vm.$on('update:isShow', isShow => {
        vm.isShow = isShow;
    });

    // 
    vm.close = () => {
        vm.isShow = false;
    };
    return vm;
};

Loading.close = () => {
    Vue.nextTick(() => {
        vm.isShow = false;
        // vm.$destroy();
    });
};

// : this.$toast | this.$toast.close()
Vue.prototype.$loading = Loading;


