<template>
    <main class="fill">
        <a-button @click="open">this.$loading(), 3</a-button>
    </main>
</template>
<script>
export default {
    name: 'GlboalLoadingDemo',

    mounted() {
        this.open();
    },

    methods: {
        open() {
            this.$loading('...');
            setTimeout(() => {
                this.$loading.close();
                this.$toast('', { type: 'success'});
            }, 1200);
        },
    },
};
</script>


