<template>
    <v-scroll-view class="fill">
        <v-popup :is-show.sync="isShow" :from="from">
            <!-- <template slot="header">
                
            </template> -->
            <v-cell></v-cell>
            <v-cell></v-cell>
            <v-cell></v-cell>

            <div class="fill">
                <v-button type="primary" ></v-button>
                <v-button outline  @click="isShow=false" class="gutter-top"></v-button>
            </div>
        </v-popup>

        <v-button outline  @click="handle('bottom')" class="gutter-top">
            
        </v-button>

        <v-button outline  @click="handle('top')" class="gutter-top">
            
        </v-button>


        <v-button outline  @click="handle('left')" class="gutter-top">
            
        </v-button>

        <v-button outline  @click="handle('right')" class="gutter-top">
            
        </v-button>

    </v-scroll-view>
</template>
<script>
import VPopup from '@/packages/Popup/Popup';
import VCell from '@/packages/Cell/Cell';
import VButton from '@/packages/Button/Button';
import VScrollView from '@/packages/ScrollView/ScrollView';
export default {
    name: 'PopupDemo',

    data() {
        return {
            from: 'top',
            isShow: false
        };
    },

    methods: {
        handle(from){
            this.from = from;
            this.isShow = true;
        }
    },

    components: {
        VButton,
        VPopup,
        VCell,
        VScrollView
    }
};
</script>
<style scoped lang="scss">

</style>



