<template>
    <div class="atom-scroll-view" @scroll="scroll">
        <slot></slot>
    </div>
</template>
<script>
import { getHeight, getScrollTop } from '../../utils/dom';
import debounce from 'lodash/debounce';
export default {
    name: 'AtomScrollView',

    props: {
        /**
         * 
         */
        value: {
            type: Number,
            default: 0
        },
        /**
         * 
         */
        threshold: {
            type: Number,
            default: 50
        },
        /**
         * 
         */
        isListenBottom: {
            type: Boolean,
            default: true
        },
        /**
         * offset
         */
        keyboardOffset: {
            type: Number,
            default: 60
        }
    },

    data() {
        return {
            viewHeight: 0,
            winHeight: 0,
            direction: '',
            scrollTop: 0,
            formInput: null
        };
    },

    mounted() {
        this.viewHeight = getHeight(this.$el);
        window.addEventListener('resize', this._resizeCalc);
        this._markInput();
    },

    methods: {
        // touchscrollTo
        stop(e){
            if(0 == this.scrollTop) {
                this.scrollTo(1);
                e.preventDefault();
            }
        },
        /**
         * 
         */
        _resizeCalc() {
            debounce(() => {
                this.viewHeight = getHeight(this.$el);
                this.winHeight = getHeight(window);
                let node = this.formInput;
                if (null !== node) {
                    // node.scrollIntoView(false);
                    // scrollIntoView
                    // keyboardOffset
                    const {
                        bottom,
                        height,
                        top
                    } = node.getBoundingClientRect();
                    const scrollTop =
                        this.$el.scrollTop +
                        top -
                        this.winHeight +
                        height +
                        this.keyboardOffset;
                    this.scrollTo(scrollTop);
                    node = null;
                }
            }, 200)();
        },
        /**
         * input/textarea
         * resize
         */
        _markInput() {
            // inputtextara
            // 
            this.$el.addEventListener('click', e => {
                var node = e.target;
                var nodeName = node.nodeName.toLowerCase();
                // input/textarea
                if ('input' === nodeName || 'textarea' === nodeName) {
                    this.formInput = node;
                }
            });
        },
        /**
         * 
         */
        _checkBottom: debounce(function() {
            if (this.isListenBottom && 'down' == this.direction) {
                this.contentHeight = getHeight(this.$el, { isScroll: true });
                // 
                if (
                    this.contentHeight <
                    this.scrollTop + this.viewHeight + this.threshold
                ) {
                    this.$emit('scroll-bottom', this.scrollTop);
                }
            }
        }, 200),
        /**
         * 
         */
        scroll() {
            this.scrollTop = getScrollTop(this.$el);

            //  2018-02-01
            // 
            // Api, 
            this.viewHeight = getHeight(this.$el);
            this._checkBottom();
            this.$emit('input', this.scrollTop);
        },
        /**
         * 
         * @argument {Number}
         */
        scrollTo(top) {
            this.$el.scrollTop = top;
        }
    },

    watch: {
        value(value) {
            this.scrollTo(value);
        },

        scrollTop(oldVal, newVal) {
            this.direction = oldVal < newVal ? 'up' : 'down';
        },

        direction(v) {
            this.$emit('change-direction', v);
        }
    },

    destroyed() {
        window.removeEventListener('resize', this._resizeCalc);
    }
};
</script>


