<template>
    <a-scroll-view class="fill flex">
        <a-mask :is-show.sync="isShow">
            <div style="margin-top:90%;" class="fill">
                <p class="text-lightest"> </p>
                <a-button type="lightest" outline @click="isShow=false" class="gutter-top"></a-button>
            </div>
        </a-mask>

        <a-button outline @click="handle('bottom')" class="gutter-top flex-item--center">
            mask
        </a-button>

    </a-scroll-view>
</template>
<script>
export default {
    name: 'MaskDemo',

    data() {
        return {
            isShow: false
        };
    },

    methods: {
        handle(from) {
            this.isShow = true;
        }
    }
};
</script>
<style scoped lang="scss">
</style>



